import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {FormsModule, ReactiveFormsModule} from "@angular/forms";

import {AppComponent} from './app.component';
import {PartiesComponent} from './components/parties/parties.component';
import {AppRoutingModule} from "./app-routing.module";
import {ServerService} from "./services/server-service";
import {HttpModule, JsonpModule} from "@angular/http";
import {AgGridModule} from 'ag-grid-angular/main';
import {PartyComponent} from './components/parties/party/party.component';
import {PartyMainComponent} from './components/parties/party/party-main/party-main.component';
import {PartyJobsComponent} from './components/parties/party/party-jobs/party-jobs.component';
import {PartyInvoicesComponent} from './components/parties/party/party-invoices/party-invoices.component';
import {PartyBillsComponent} from './components/parties/party/party-bills/party-bills.component';
import {FormValidationService} from "./services/form-validation.service";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {JobsComponent} from "./components/jobs/jobs.component";
import { JobComponent } from './components/jobs/job/job.component';
import { JobMainComponent } from './components/jobs/job/job-main/job-main.component';
import { JobCommentsComponent } from './components/jobs/job/job-comments/job-comments.component';
import { JobScheduleComponent } from './components/jobs/job/job-schedule/job-schedule.component';
import { JobFieldWorkComponent } from './components/jobs/job/job-field-work/job-field-work.component';
import { JobBillsComponent } from './components/jobs/job/job-bills/job-bills.component';
import { JobCostsComponent } from './components/jobs/job/job-costs/job-costs.component';
import { JobInvoicesComponent } from './components/jobs/job/job-invoices/job-invoices.component';
import { PartyCommentsComponent } from './components/parties/party/party-comments/party-comments.component';
import { FormInputDirective } from './directives/form-input.directive';

@NgModule({
  declarations: [
    AppComponent,
    PartiesComponent,
    PartyComponent,
    PartyMainComponent,
    PartyJobsComponent,
    PartyInvoicesComponent,
    PartyBillsComponent,
    JobsComponent,
    JobComponent,
    JobMainComponent,
    JobCommentsComponent,
    JobScheduleComponent,
    JobFieldWorkComponent,
    JobBillsComponent,
    JobCostsComponent,
    JobInvoicesComponent,
    PartyCommentsComponent,
    FormInputDirective,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    NgbModule.forRoot(),
    AppRoutingModule,
    AgGridModule.withComponents([]),
    JsonpModule
  ],
  providers: [ServerService, FormValidationService],
  bootstrap: [AppComponent]
})
export class AppModule {
}



