import {AfterViewInit, ChangeDetectorRef, Component, HostListener, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../services/server-service";
import {GridOptions} from "ag-grid";
import {Subscription} from "rxjs/Subscription";
import {NavBarService} from "../../services/nav-bar.service";

@Component({
  selector: 'app-jobs',
  templateUrl: './jobs.component.html',
  styleUrls: ['./jobs.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JobsComponent implements OnInit, AfterViewInit {
  gridOptions: GridOptions;
  rowData: any[] = [];
  navBarSubscription: Subscription;
  showDropdown = false;


  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private changeDetectorRef: ChangeDetectorRef) {
  }

  @HostListener('document:keyup', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.keyCode === 27) {
      console.log('Escape!');
      console.log(event);
      this.showDropdown = false;
    }
  }

  ngOnInit() {
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    };
    this.serverService.getAllJobs()
      .subscribe((response: any) => {
        this.rowData = response._embedded.jobs;
        this.formatRowData();
      }, (error) => {
        console.log(error);
      });
    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      // the timings of the nav bar css transition should be considered
      console.log("sizeColumnsToFit");
      setTimeout(() => {
        if (this.gridOptions && this.gridOptions.api) {
          this.gridOptions.api.sizeColumnsToFit();
        }
      }, 200);
    });
  }


  ngAfterViewInit() {
    this.gridOptions.api.sizeColumnsToFit();
  }

  formatRowData() {
    for (let i = 0; i < this.rowData.length; i++) {
      console.log(this.rowData[i]);
      this.rowData[i].latestSchedule.latestSchedule =
        this.rowData[i].latestSchedule.logicalDay ?
          this.rowData[i].latestSchedule.logicalDay :
          this.rowData[i].latestSchedule.latestSchedule;
      // let id = this.job[i]._links.self.href
      //   .substr(this.job[i]._links.self.href.lastIndexOf('/') + 1);
      // this.job[i].jobId = id;
    }
  }


  columnDefs = [
    {
      headerName: "Id",
      field: 'jobNumber',
      cellClass: 'app-table-cell first-column',
      width: 70
    },
    {
      headerName: "Customer",
      field: "customerName",
      cellClass: 'app-table-cell second-column',
      width: 230,
    },
    {
      headerName: "Referral",
      field: "referral",
      cellClass: 'app-table-cell third-column',
      width: 120,
    },
    {
      headerName: "Current Type",
      field: "type",
      cellClass: 'app-table-cell fourth-column',
      width: 150,
    },
    {
      headerName: "Status",
      field: "status",
      cellClass: 'app-table-cell fifth-column',
      width: 180,
    },
    {
      headerName: "Address",
      field: "addressStr",
      cellClass: 'app-table-cell sixth-column'
    },
    {
      headerName: "Latest Schedule",
      field: "latestSchedule.latestSchedule",
      cellClass: 'app-table-cell seventh-column',
      width: 160,
    },
    {
      headerName: "Priority",
      field: "priority",
      cellClass: 'app-table-cell third-column',
      width: 100,
    }
  ];

  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event);
    $event.cancelBubble;
    if ($event.column.colId === "customerName") {
      this.router.navigate(['parties', $event.data.customerUid, 'main'])
        .then((value) => {
          console.log(value);
        }).catch((error) => {
        console.log(error)
      });
    }

    if ($event.column.colId === "jobNumber") {
      let href = $event.data._links.self.href.substr($event.data._links.self.href.lastIndexOf('/') + 1);
      this.router.navigate([href, 'main'], {relativeTo: this.activatedRoute})
        .then((value) => {
          console.log(value);
        }).catch((error) => {
        console.log(error)
      });
    }
  }

  onCellDoubleClicked($event) {

  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
  }

  toggleDropdown() {
    this.showDropdown = !this.showDropdown;
  }

  onNewQuoteRequest() {
    console.log('onNewQuoteRequest');
    this.toggleDropdown();
  }

  onNewServiceRequest() {
    console.log('onNewServiceRequest');
    this.toggleDropdown();
  }

}



