import {AfterViewInit, ChangeDetectorRef, Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {GridOptions} from "ag-grid";
import {Subscription} from "rxjs/Subscription";
import {NavBarService} from "../../../../services/nav-bar.service";
import {PartyService} from "../../../../services/party.service";

@Component({
  selector: 'app-party-invoices',
  templateUrl: './party-invoices.component.html',
  styleUrls: ['./party-invoices.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartyInvoicesComponent implements OnInit, AfterViewInit {
  paramsReceived: string = null;
  partyInvoices: any;
  gridOptions: GridOptions;
  rowData: any[];
  navBarSubscription: Subscription;
  partyServiceSubscription: Subscription;
  googleMapsClient: any;


  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private router: Router,
              private activatedRoute: ActivatedRoute,
              private partyService:PartyService) {

  }

  ngOnInit() {
    this.partyServiceSubscription = this.partyService.partySourceObservable.subscribe((data) => {
      if (data && data.id && data.type === PartyService.TYPE_INVOICES) {
        this.paramsReceived = data.id;
        this.serverService.getRequestByPartyId(this.paramsReceived, '/invoices')
          .subscribe((invoices: any) => {
            console.log(invoices);
            if (invoices._embedded) {
              this.rowData = invoices._embedded.invoices;
            }
            else {
              this.rowData = null;
            }
          }, (error) => {
            console.log(error);
          });
        console.log(data.data);
      }
      else {

      }
    }, (error) => console.log(error));
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    };

    this.navBarSubscription = this.navBarService.getNavBarSubject().subscribe((value) => {
      console.log("sizeColumnsToFit");
      this.gridOptions.api.sizeColumnsToFit();
    });
  }

  newCustomer() {

  }

  columnDefs = [
    {
      headerName: "Invoice id",
      field: 'description',
      cellClass: 'first-column app-table-cell'
    },
    {
      headerName: "Job id",
      field: "id",
      cellClass: 'second-column app-table-cell'
    },
    {
      headerName: "Invoice date",
      field: "invoice_dt",
      cellClass: 'third-column app-table-cell'
    },
    {
      headerName: "Due date",
      field: "invoice_due_date",
      cellClass: 'fourth-column app-table-cell'
    },
    {
      headerName: "Total amount",
      field: "total_amount",
      cellClass: 'fifth-column app-table-cell'
    }
  ];


  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data);
    let href = $event.data._links.self.href;
    let index = (href.lastIndexOf('/') + 1);
    let id = +(href.substr(index));
    console.log(id);
    // this.router.navigate(['job', id, 'invoices'])
    //   .then((value) => {
    //     console.log(value);
    //   }).catch((error) => {
    //   console.log(error)
    // });
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.partyService.announceParentComponent(PartyService.TYPE_INVOICES);
    this.gridOptions.api.sizeColumnsToFit();
  }

  ngOnDestroy() {
    this.navBarSubscription.unsubscribe();
    this.partyServiceSubscription.unsubscribe();
  }
}



