import {Component, OnDestroy, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {ServerService} from "../../../../services/server-service";
import {PartyService} from "../../../../services/party.service";
import {Subscription} from "rxjs/Subscription";
import {FormControl, FormGroup, Validators} from "@angular/forms";

@Component({
  selector: 'app-party-jobs',
  templateUrl: './party-jobs.component.html',
  styleUrls: ['./party-jobs.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PartyJobsComponent implements OnInit, OnDestroy {

  paramsReceived: string = null;
  partyJobs: any;
  partyServiceSubscription: Subscription;
  path: string;
  newJob: boolean = false;
  jobForm: FormGroup;
  jobStatuses: any = null;
  showDropdownJobStatuses = false;
  jobStatusesPath: string = "http://localhost:8080/api/jobStatuses";

  constructor(private activatedRoute: ActivatedRoute, private serverService: ServerService,
              private partyService: PartyService) {
  }

  ngOnInit() {
    this.getJobStatuses();
    this.partyServiceSubscription = this.partyService.partySourceObservable
      .subscribe((data) => {
        if (data && data.id && data.type === PartyService.TYPE_JOBS) {
          this.paramsReceived = data.id;
          this.path = "http://localhost:8080/api/parties/" + this.paramsReceived + "/jobs";
          this.getUserJobs();
        }
      });
  }

  getUserJobs() {
    this.serverService.getRequestByPath(this.path)
      .subscribe(
        (jobs: any) => {
          console.log(jobs);
          this.partyJobs = jobs._embedded.jobs;
        },
        (error) => {
          console.log(error);
        });
  }

  getJobStatuses() {
    this.serverService.getRequestByPath(this.jobStatusesPath)
      .subscribe(
        (jobStatuses: any) => {
          console.log(jobStatuses);
          this.jobStatuses = jobStatuses._embedded.jobStatuses;
        },
        (error) => {
          console.log(error);
        });
  }

  ngAfterViewInit() {
    this.partyService.announceParentComponent(PartyService.TYPE_JOBS);
  }

  ngOnDestroy() {
    this.partyServiceSubscription.unsubscribe();
  }

  createNewJob() {
    console.log("create new comment");
    this.createJobForm();
    this.newJob = true;
  }

  createJobForm() {
    this.jobForm = new FormGroup({
      'job-description': new FormControl(null, [Validators.required]),
      'job-status': new FormControl(null, [Validators.required, this.validateJobStatus.bind(this)])
    });
  }

  dontSaveJob() {
    this.jobForm.reset();
    this.newJob = false;
  }

  toggleDropdownJobStatuses() {
    this.showDropdownJobStatuses = !this.showDropdownJobStatuses;
  }

  onJobStatusSelected(status) {
    console.log(status);
    if (this.jobForm && this.jobForm.get('job-status')) {
      this.jobForm.get('job-status').setValue(status.status);
    }
  }

  validateJobStatus(formControl: FormControl): { [key: string]: boolean } {
    for(let i = 0; i < this.jobStatuses.length; i++) {
       if (formControl.value === this.jobStatuses[i].status) {
         return null;
       }
    }
    return {'InvalidJobStatus': true};
  }


  onSubmit() {
    if (this.jobForm.touched && this.jobForm.valid) {
      this.saveJob();
    }
  }

  private saveJob() {

  }

}



