import {AfterViewInit, Component, OnInit, ViewEncapsulation} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {ServerService} from "../../../services/server-service";
import {Location} from "@angular/common";
import {PartyService} from "../../../services/party.service";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'app-party',
  templateUrl: './party.component.html',
  styleUrls: ['./party.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [PartyService]
})
export class PartyComponent implements AfterViewInit, OnInit {
  partyBills: any = null;
  partyInvoices: any = null;
  partyJobs: any = null;
  data: any;
  paramsReceived: string;
  childComponentSubscription: Subscription;

  constructor(private activatedRoute: ActivatedRoute,
              private router: Router,
              private serverService: ServerService,
              private location: Location,
              private partyService: PartyService) {
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params['id']);
      this.paramsReceived = params['id'];
      this.makeDataRequest();
    }, (error) => console.log(error));
  }

  ngAfterViewInit() {
    this.childComponentSubscription = this.partyService.childComponentObservable
      .subscribe((data) => {
        if (data === PartyService.TYPE_MAIN) {
          this.makePartyRequest();
        }
        if (data === PartyService.TYPE_JOBS) {
          this.makeJobsRequest();
        }
        if (data === PartyService.TYPE_INVOICES) {
          this.makeInvoicesRequest();
        }
        if (data === PartyService.TYPE_BILLS) {
          this.makeBillsRequest();
        }
        if (data === PartyService.TYPE_COMMENTS) {
          this.makeCommentsRequest();
        }
      });

  }

  makeDataRequest() {
    this.makePartyRequest();
    this.makeJobsRequest();
    this.makeBillsRequest();
    this.makeInvoicesRequest();
    this.makeCommentsRequest()
  }

  makePartyRequest() {
    this.serverService.getRequestByPartyId(this.paramsReceived, null, null)
      .subscribe((data: any) => {
        console.log(data);
        this.data = data;
        this.partyService.announcePartyDetail({id:this.paramsReceived, type:PartyService.TYPE_MAIN});
      }, (error) => {
        console.log(error);
      });
  }

  makeCommentsRequest() {
    this.partyService.announcePartyDetail({id:this.paramsReceived, type:PartyService.TYPE_COMMENTS});
  }

  makeJobsRequest() {
    this.serverService.getRequestByPartyId(this.paramsReceived, '/jobs')
      .subscribe((jobs: any) => {
        this.partyJobs = jobs._embedded.jobs;
        this.partyService.announcePartyDetail({id:this.paramsReceived, type:PartyService.TYPE_JOBS});
      }, (error) => {
        console.log(error);
      });
  }

  makeBillsRequest() {
    this.serverService.getRequestByPartyId(this.paramsReceived, '/bills')
      .subscribe((bills: any) => {
      console.log(bills);
        this.partyBills = bills._embedded.bills;
        this.partyService.announcePartyDetail({id:this.paramsReceived, type:PartyService.TYPE_BILLS});
      }, (error) => {
        console.log(error);
      });
  }

  makeInvoicesRequest() {
    this.serverService.getRequestByPartyId(this.paramsReceived)
      .subscribe((invoices: any) => {
        if (invoices._embedded) {
          this.partyInvoices = invoices._embedded.invoices;
        }
        else {
          this.partyInvoices = null;
        }
        this.partyService.announcePartyDetail({id:this.paramsReceived, type:PartyService.TYPE_INVOICES});
      }, (error) => {
        console.log(error);
      });
  }

  onTabSelected() {
  }
}



