import { Injectable } from '@angular/core';
import {Subject} from "rxjs/Subject";



@Injectable()
export class PartyService {

  static readonly TYPE_MAIN = 'TYPE_MAIN';
  static readonly TYPE_JOBS = 'TYPE_JOBS';
  static readonly TYPE_BILLS = 'TYPE_BILLS';
  static readonly TYPE_INVOICES = 'TYPE_INVOICES';
  static readonly TYPE_COMMENTS = 'TYPE_COMMENTS';


  partySource = new Subject<any>();
  partySourceObservable = this.partySource.asObservable();

  childComponentSubject = new Subject<string>();
  childComponentObservable = this.childComponentSubject.asObservable();

  constructor() { }

  announcePartyDetail(data) {
    console.log(data);
    this.partySource.next(data);
  }

  announceParentComponent(data) {
    setTimeout(()=>{
      this.childComponentSubject.next(data);
    }, 100);

  }
}



