import {AfterViewInit, Component, HostListener, OnInit, ViewEncapsulation} from '@angular/core';
import {FormControl, FormGroup, SelectControlValueAccessor} from "@angular/forms";
import {Subscription} from "rxjs/Subscription";
import {JobService} from "../../../../services/job.service";
import {ServerService} from "../../../../services/server-service";

@Component({
  selector: 'app-job-main',
  templateUrl: './job-main.component.html',
  styleUrls: ['./job-main.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JobMainComponent implements OnInit, AfterViewInit {
  customers = ["Aadsfsadf", "dafasdfasdf"];
  jobForm: FormGroup;
  customersAutoSuggestion = [];// ['Aadsf'];
  showJobPriorityDropdown = false;
  showJobStatusDropdown = false;
  customersAutoSuggestSubscription: Subscription;
  jobServiceSubscription: Subscription;
  selectedPriority = "Select Priority";
  paramsReceived: string;
  job: any;


  constructor(private serverService: ServerService,
              private jobService: JobService) {
  }

  jobPriorities = ["Normal", "Urgent", "Important"];
  jobStatuses = [];

  @HostListener('click', ['$event'])
  hostClicked($event) {
    console.log($event.target.id);
    if ($event.target.id != 'job-priority') {
      this.showJobPriorityDropdown = false;
    }
    if ($event.target.id != 'job-status') {
      this.showJobStatusDropdown = false;
    }
  }

  @HostListener('document:keyup', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.keyCode === 27) {
      console.log('Escape!');
      console.log(event);
      this.showJobStatusDropdown = false;
      this.showJobPriorityDropdown = false;
    }
  }

  ngOnInit() {
    this.createJobForm();
    this.jobServiceSubscription = this.jobService.jobSourceObservable
      .subscribe((data) => {
        console.log(data);
        if (data && data.id && data.type === JobService.TYPE_MAIN) {
          this.paramsReceived = data.id;
          this.serverService.getRequestByJobId(this.paramsReceived)
            .subscribe((response) => {
              this.job = response;
              this.setJobType();
              this.setJobForm();
            }, (error) => {
              console.log(error);
            })
        }
      });
    this.initCustomersAutoSuggest();
  }

  ngAfterViewInit() {
    this.jobService.announceParentComponent({type: JobService.TYPE_MAIN});
  }

  createJobForm() {
    this.jobForm = new FormGroup({
      'userData': new FormGroup({
        'customer-name': new FormControl(null),
        'job-address': new FormControl(null),
        'job-type': new FormControl(null),
        'job-referral': new FormControl(null),
        'job-priority': new FormControl(null),
        'job-status': new FormControl("ABC"),
        'job-detail': new FormControl(null)
      })
    });
  }

  setJobForm() {
    console.log(this.job);
    this.jobForm.setValue({
      'userData': {
        'customer-name': this.job.customerName,
        'job-address': this.job.addressStr,
        'job-type': this.job.type,
        'job-referral': this.job.referral,
        'job-priority': this.job.priority,
        'job-status': this.job.status,
        'job-detail': this.job.description,
      }
    });
    console.log(this.jobForm.get('userData.job-priority').value);
  }

  setJobType() {
    let jobType = this.job.type;
    let pathRequest = "http://localhost:8080/api/jobTypes/" + jobType + "/jobStatuses";
    this.serverService.getRequestByPath(pathRequest)
      .subscribe((response)=> {
        this.jobStatuses = response._embedded.jobStatuses;
        console.log(response);
      }, (error)=>{console.log(error)});
  }

  onSubmit() {

  }

  formatJob() {

  }

  onCustomerSelected(customer) {
    console.log(customer)
  }

  onCustomerHover($event, customer) {
    console.log(customer);
  }

  toggleJobPriority() {
    this.showJobPriorityDropdown = !this.showJobPriorityDropdown;
    this.showJobStatusDropdown = false;
  }

  onSelectJobPriority(title) {
    this.selectedPriority = title;
  }

  toggleJobStatus() {
    this.showJobStatusDropdown = !this.showJobStatusDropdown;
    this.showJobPriorityDropdown = false;
  }

  onSelectJobStatus(title) {
    console.log(title);
  }

  initCustomersAutoSuggest() {
    this.customersAutoSuggestSubscription = this.jobForm.get('userData.customer-name')
      .valueChanges.debounceTime(400)
      .subscribe(
        (value) => {
          console.log(value);
        }, (error) => {
          console.log(error);
        }
      );
  }

}



