import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {jobTabs} from "./job-tabs";
import {ActivatedRoute} from "@angular/router";
import {JobService} from "../../../services/job.service";
import {ServerService} from "../../../services/server-service";
import {Subscription} from "rxjs/Subscription";


@Component({
  selector: 'app-job',
  templateUrl: './job.component.html',
  styleUrls: ['./job.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [JobService]
})
export class JobComponent implements OnInit {

  jobTabs = jobTabs;
  paramsReceived: string;
  childComponentSubscription:Subscription;

  constructor(private serverService: ServerService, private activatedRoute: ActivatedRoute,
              private jobService: JobService) {
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params) => {
      console.log(params);
      this.paramsReceived = params['id'];
      this.makeMultipleRequests();
    }, (error) => {
      console.log(error)
    });
    this.childComponentSubscription = this.jobService.childComponentObservable
      .subscribe((data)=>{
        if (data.type === JobService.TYPE_MAIN) {
            this.announceToMain();
        }
    }, (error) => {console.log(error);})
  }

  makeMultipleRequests() {
    this.announceToMain();
  }

  announceToMain() {
    this.jobService.announceJobDetail({id: this.paramsReceived, type: JobService.TYPE_MAIN});
  }

}



